

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.card {
  border-radius: 2px;
  height: 170px;
  margin: 10px 16px 0px 16px;
  position: relative;
  width: 195px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  float: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: gray;
}
.card > .img-container > img {
  width: 100%
}

.card:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}

.navbar {
  background: lightblue;
  border-radius: 2px;
  margin-top: 0px !important;
}

.nav-item {
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}
body {
  background: lightgray;
}
